@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,800&display=swap");
@import url("https://fonts.cdnfonts.com/css/common-pixel");
@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@500;700&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Poppins", sans-serif;
  color: black;
}

body {
  background-color: #f5f5f5;
}

.RestaurantBilling_section {
  color: #252525;
  max-width: 130rem;
  margin: 0 auto;
  height: 80vh;
  position: relative;
  padding: 0 3.6rem;
  margin-top: 2rem;
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #f5f5f5;
  height: 10vh;
  background-image: linear-gradient(to left bottom, #20c997, #087f5b);
  margin: 0rem 0rem 0.5rem 0rem;
}

.PageOptions {
  display: flex;
  justify-content: space-between;

  font-size: 1.6rem;
  font-weight: 600;

  width: 14rem;
}

.Main_heading {
  text-align: center;
  font-size: 2rem;
  font-family: "Oswald", sans-serif;
}

.soSub_heading {
  font-size: 3rem;
  margin-top: 3rem;
  text-align: center;
  font-family: "Josefin Sans", sans-serif;
}

.Menu_items_available {
  display: flex;
  justify-content: center;
  border-collapse: separate;
  border-spacing: 20px 10px;
}

.menuItems {
  display: none;
}

.Menu_items_available td {
  font-size: 1.4rem;
}
.Menu_items_available td:nth-child(2) {
  margin-left: 2rem;
}

.Working_div {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  column-gap: 3rem;
}

.choose_items_box {
  /* grid-row-start: 2; */
  /* grid-row-end: 3; */
  align-self: center;
  justify-self: center;
  padding: 2rem 1rem;

  display: grid;
  grid-template-rows: 1fr 1f 1fr;
  row-gap: 2rem;

  align-items: center;
  justify-items: center;
}

.take_inputs {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  grid-column-start: 1;
  grid-column-end: 3;
}

#quantityInput {
  padding: 1.2rem 1rem;
  font-size: 1.8em;
  border: none;
  border: 2px solid #087f5b;
  /* width: 10em; */
  text-transform: capitalize;
  background-color: #64e3bd;
  border-radius: 4px;
}

#quantityInput:focus {
  outline: none;
  opacity: 1;
}

#itemChoosen {
  color: black;
  padding: 1.2rem 1rem;
  font-family: "Poppins", sans-serif;
  text-transform: capitalize;
  opacity: 1;
  font-weight: 600;
  border: none;
  /* border-bottom: 2px solid black; */
  flex-grow: 1;
  background-color: #20c997;
  border-radius: 4px;
}

#itemChoosen[placeholder] {
  opacity: 1;
  color: red;
}

#itemChoosen:focus {
  outline: none;
}

.item_cost_preview {
  text-align: center;
  font-size: 2rem;
  color: black;
  align-self: center;

  grid-column: 1/3;
}

.SubmitButton {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 600;

  justify-self: center;
  padding: 1.2rem 2rem;
  background-color: #e8590c;
  border-radius: 4px;
  border: none;

  grid-row-start: 3;
  grid-column: 1/3;
}

.SubmitButton:focus {
  outline: none;
}

.Billing_container {
  background-color: #e3c1eb;
  grid-row-start: 1;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 6;

  padding: 2rem 2em 2rem 2rem;
  color: #252525;

  height: 40rem;
  /* background-color: red; */
  overflow-y: scroll;
  overflow-x: none;
  border-radius: 4px;
}

/*  right */
.simple_heading {
  text-align: center;
  font-size: 2rem;
  text-transform: capitalize;
}

.items_choosen_table {
  width: 100%;
  border-collapse: collapse;
}

.items_choosen_table tbody tr {
  height: 4rem;
  border-bottom: 1px solid #862e9c;
}

.items_choosen_table tbody tr td:nth-child(4) {
  /* background-color: #ffd8a8; */
  font-weight: 600;
}

.items_choosen_table td {
  font-size: 1.4rem;
  text-align: center;
}

.headdingTR {
  /* background-color: #e8590c; */
  background-image: linear-gradient(to right bottom, #862e9c, #862e9c);
  height: 4rem;
  color: #f4f4f4;
  margin-bottom: 2rem;
}

.headdingTR td:nth-child(2) {
  width: 15rem;
}

.items_td {
  padding: 1rem 0rem;
  height: 3rem;
}

.items_td td:nth-child(4) {
  background-color: #ffc078;
}
.totalling_table {
  grid-row-start: 4;
  grid-column: 4/6;

  display: flex;
  flex-direction: column;
  justify-self: end;

  border-collapse: separate;
  border-spacing: 30px 2px;

  margin-top: 2rem;

  align-self: center;
  justify-self: center;
}

.totalling_table td {
  font-size: 1.4rem;
  color: #252525;

  padding: 0.3rem 1rem;
}

.totalling_table td:nth-child(2) {
  width: 13rem;
  /* background-color: #e8590c; */
}
.totalling_table td:nth-child(1) {
  font-weight: 700;
}

.selectOption {
  background-color: white;
  /* height: 10rem; */
  padding: 3rem;
}

.totalling_table tr:nth-child(3) > td:nth-child(2) {
  background-color: #e8590c;
  color: #f5f5f5;
}
